/**
 */
export class Offer {

  /**
   * @param {string} skuId
   * @param {string} title
   * @param {string} description
   * @param {string} price
   */
  constructor(skuId, title, description, price) {
    /** @const {string} */
    this.skuId = skuId;
    /** @const {string} */
    this.title = title;
    /** @const {string} */
    this.description = description;
    /** @const {string} */
    this.price = price;
  }
}
